.chatParent {
    height: 100vh;
    max-height: 100vh;
}
.navigator {
    display: flex;
    width: 100vw;
    height: 80px;
    background: rgb(0, 78, 122);
    align-items: center;
    color: white;
    font-size: 22px;
}
.navigator div {
    display: flex;
    margin: 20px;
    justify-content: space-between;
    width: 100%;
}
.navigator a {
    color: white;
    font-size: 26px;
    text-decoration: none;
}
.listRoom {
    display: flex;
    height: 100%;
    min-width: 300px;
    background: rgb(41, 41, 41);
    flex-direction: column;
}
.container {
    display: flex;
    height: calc(100% - 80px);
    flex-direction: row;
}
.chat {
    display: flex;
    height: 100%;
    width: 100%;
    max-width: calc(100% - 300px);
    flex-direction: column;
}
.msgbox {
    display: flex;
    height: calc(100% - 45px);
    word-wrap: break-word;
    max-width: 100%;
    flex-direction: column;
    overflow: auto;
}
.msgbox div {
    margin-left: 10px;
    margin-top: 10px;
}
.options input {
    display: block;
    height: 20px;
    padding: 5px;
    border: 1px solid black;
    border-radius: 5px;
    width: 100%;
    margin-top: 5px;
}
.options input:focus {
    outline: none;
}
.chatinput {
    display: block;
    width: 100vw;
}
.options {
    display: flex;
    margin: 5px;
    flex-direction: column;
    width: calc(100vw - 310px);
}
.options button {
    border: none;
    border-radius: 5px;
    padding: 5px 10px;
    background: rgb(0, 111, 202);
    color: white;
    height: calc(100% - 5px);
    margin-top: 5px;
    cursor: pointer;
    margin-left: 20px;
}
.options button:hover {
    background: rgb(49, 58, 65);
}
.inputParent {
    display: flex;
    flex-direction: row;
    margin-right: 15px;
    height: auto;
    width: 100%;
}
.inputParent div {
    height: 100%;
}
.command {
    display: block;
    padding: 5px;
    border: 1px solid black;
    border-radius: 5px;
    width: calc(100% - 15px);
    overflow-y: auto;
    max-height: 200px;
}
.listRoom div {
    width: 100%;
    color: white;
    background: rgb(15, 15, 15);
    cursor: pointer;
    text-align: center;
    padding: 15px 0;
    margin-top: 5px;
}
.listRoom div:hover {
    background: rgb(82, 82, 82);
}

@media (max-width: 720px) {
    .listRoom {
        display: none;
    }
    .chat {
        max-width: 100%;
    }
    .options {
        max-width: calc(100vw - 10px);
        width: 100%;
    }
}