.newContent {
    display: block;
    margin: auto;
    padding: 10px 20px;
    background: rgb(0, 123, 206);
    border: none;
    color: white;
    margin-top: 10px;
    border-radius: 5px;
    cursor: pointer;
}
.newContent:hover {
    background: rgb(0, 70, 117);
}
.showPanel {
    position: fixed;    
    z-index: 900;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.7);
}
.showPanel .body {
    display: flex;
    width: 500px;
    height: 300px;
    background: white;
    box-shadow: 1px 1px 10px rgb(0, 0, 0);
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border-radius: 5px;
}
.showPanel input {
    display: block;
    padding: 5px;
    border: 1px solid black;
    border-radius: 5px;
    width: calc(100% - 10px);
}
.showPanel .body div {
    display: flex;
    width: calc(100% - 20px);
    margin: 10px;
    flex-wrap: wrap;
    align-items: center;
    flex-direction: column;
}
.showPanel input:focus {
    outline: none;
}
.showPanel button {
    display: block;
    border-radius: 5px;
    width: 100%;
    border: none;
    padding: 5px;
    background: rgb(0, 76, 190);
    color: white;
    cursor: pointer;
}
.showPanel button:hover {
    background: rgb(0, 57, 131);
}
.showPanel h3 {
    display: block;
    width: 100%;
    margin-bottom: 10px;
    text-align: center;
    padding-bottom: 5px;
    border-bottom: 1px solid gray;
}
.listTopic {
    display: block;
    width: 100%;
}
.listTopic a {
    display: block;
    width: calc(100% - 40px);
    margin: 10px;
    border: 1px solid black;
    padding: 10px;
    border-radius: 5px;
    color: black;
    text-align: center;
    text-decoration: none;
}
.listTopic a:hover {
    background: rgb(85, 85, 85);
}
@media (max-width: 520px) {
    .showPanel .body {
        margin: 10px;
    }
}