.staff {
    display: block;
    border: 1px solid black;
    border-radius: 5px;
    background: none;
    padding: 5px 10px;
    margin: auto;
    margin-top: 10px;
    cursor: pointer;
}
.staff:hover {
    background: rgb(102, 102, 102);
}
.showAllStaff {
    position: fixed;
    top: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,0.7);
    z-index: 980;
}
.showAllStaff .overflow {
    position: absolute;
    background: white;
    padding: 5px;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    box-shadow: 1px 1px 4px black;
    border-radius: 5px;
    max-height: calc(100vh - 20px);
    overflow: auto;
}
.showAllStaff table {
    text-align: center;
}
.chatroombtn {
    display: block;
    text-align: center;
    margin: auto;
    margin-top: 10px;
    color: white;
    text-decoration: none;
    padding: 5px 10px;
    width: 230px;
    background: rgb(0, 98, 179);
    border-radius: 5px;
}
.chatroombtn:hover {
    background: rgb(0, 32, 58);
}