.notify {
    position: absolute;
    width: 300px;
    height: 400px;
    border: 1px solid black;
    border-radius: 5px;
    padding: 5px;
    top: 80px;
    background: white;
    right: 20px;
}
.notify h3 {
    font-size: 26px;
}
.notifyBody {
    display: flex;
    flex-direction: column;
}
.notifyParent {
    display: flex;
    width: 40px;
    height: 40px;
    background: rgb(216, 216, 216);
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
    cursor: pointer;
}
.notifyicon {
    font-size: 24px;
    color: black;
    border-radius: 50%;
}
.notifyParent:hover {
    background: rgb(121, 121, 121);
}
.notify div {
    margin-top: 5px;
}
.backgroundNotify {
    position: fixed;
    z-index: 990;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
}
.notifyBox {
    position: fixed;
    z-index: 1000;
    top: 10px;
    right: 10px;
    border: 1px solid black;
    border-radius: 5px;
    min-width: 300px;
    overflow: none;
    background: white;
}
.notifyBox div {
    display: flex;
    flex-direction: column;
}
.notifyBox div b {
    display: block;
    width: 100%;
    text-align: center;
    border-bottom: 1px solid black;
}
.notifyBox div span {
    word-wrap: normal;
    max-height: 30px;
    padding: 5px;
}