.jobs {
    padding: 5px;
}
.jobs input {
    border: 1px solid black;
    padding: 5px;
    font-size: 16px;
}
.jobs textarea {
    resize: none;
    padding: 5px;
    width: 100%;
    height: 100px;
    border: 1px solid black;
    border-radius: 5px;
    margin-top: 5px;
    font-size: 16px;
}
.jobs div {
    margin-right: 10px;
}
.jobs button {
    width: 100%;
    padding: 5px;
    font-size: 16px;
    background: rgb(0, 90, 192);
    color: white;
    text-transform: uppercase;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
.jobs button:hover {
    background: rgb(0, 52, 112);
}

.log {
    display: block;
    padding: 5px;
    border: 1px solid black;
}