* {
    padding: 0px;
    margin: 0px;
    font-family: sans-serif;
}
.AuthBody {
    background: linear-gradient(120deg, #2980b9, #8e44ad);
    height: 100vh;
}
.formParent {
    display: flex;
    justify-content: center;
}
.form {
    display: flex;
    background: white;
    padding: 5px;
    border-radius: 10px;
    width: 350px;
    height: 500px;
    margin-top: 20px;
    justify-content: space-between;
    flex-direction: column;
}
.form .input {
    display: flex;
    flex-direction: column;
}
.form h2 {
    font-size: 36px;
    font-weight: 500;
    text-align: center;
    margin-top: 10px;
    margin-bottom: 5px;
    border-bottom: 1px solid gray;
    padding-bottom: 10px;
}
.form .input span {
    margin-top: 20px;
}
.form .input input {
    padding: 5px;
    border: 1px solid black;
    font-size: 16px;
}
.form .AuthButton {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.form .AuthButton button {
    display: block;
    width: 100%;
    background: black;
    border: none;
    color: white;
    padding: 5px;
    text-transform: uppercase;
    font-size: 17px;
    cursor: pointer
}
.form .AuthButton button:hover {
    background: rgb(20, 20, 20);
}
.form .AuthButton span {
    margin-top: 10px;
}
@media (max-width: 600px) {
    .form {
        width: 100%;
        margin: 10px;
    }
}