nav {
    display: flex;
    height: 100px;
    width: 100vw;
    background: rgb(0, 7, 29);
}
ul {
    display: flex;
    flex-direction: row;
    padding: 5px;
}
.spaceAroundMenu {
    display: flex;
    width: 100vw;
    justify-content: space-around;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
}
.Auth {
    display: flex;
    flex-direction: row;
    align-items: center;
}
nav a {
    color: white;
    text-decoration: none;
    font-size: 18px;
    padding: 10px 20px;
    border-radius: 5px;
}
nav a:hover {
    background: rgb(0, 73, 156);
}
nav li {
    list-style: none;
    color: white;
}
nav .mainNav {
    display: flex;
    align-items: center;
}
nav label {
    display: none;
    color: white;
    font-size: 50px;
    cursor: pointer;
    margin-left: 20px;
}
nav input {
    display: none;
}
nav h3 {
    font-size: 38px;
    font-weight: 500;
    color: white;
}
@media (max-width: 900px) {
    .title {
        display: none;
    }
    ul {
        position: absolute;
        z-index: 999;
        top: 0;
        flex-direction: column;
        justify-content: center;
        width: 300px;
        height: 100%;
        background: rgb(0, 7, 29);
        max-width: 300px;
        left: -300px;
        padding: unset;
    }
    .mainNav {
        display: flex;
        flex-direction: column;
    }
    .username {
        display: none !important;
    }
    nav a {
        display: block;
        text-align: center;
    }
    nav .mainNav li {
        display: block;
        width: 100%;
    }
    nav input:checked ~ ul {
        left: 0;
    }
    nav label {
        display: inline;
    }
    .activesidebar {
        position: fixed;
        left: 0;
    }
    .activeopacity {
        position: fixed;
        z-index: 998;
        left: 0;
        top: 0;
        width: 100vw;
        height: 100vh;
        background: black;
        opacity: 0.6;
    }
    .username {
        display: block;
        text-align: center;
    }
}
@media (max-width: 300px) {
    ul {
        width: 100vw;
    }
}